<template>
    <div>
        <h1>Latest news</h1>
        <MainContent :articles="articles"></MainContent>
    </div>
</template>

<script>
    import axios from 'axios'
    import MainContent from '@/components/MainContent.vue'
    export default {
        components: {
            MainContent,
        },
        data() {
            return {
                apiKey: '064f01be344c4899b067ce050322613a',
                articles: [],
                errors: []
            }
        },
        created() {
            axios.get('https://newsapi.org/v2/top-headlines?sources=techcrunch&apiKey='+this.apiKey)
            .then(response => {
                this.articles = response.data.articles
                console.log('data:')
                console.log(response.data.articles)
            })
            .catch(e => {
                this.errors.push(e)
            })
        }
    }
</script>

<style scoped>
    h1 {
        font-size: 30px;
        color: #262626;
        font-weight: 600;
        margin: 0 auto;
        text-align: center;
        display: table;
        padding-bottom: 3rem;
    }
</style>